<template>
  <v-container
    fluid
    grid-list-md>
    <core-progress-modal :show="loading" />
    <v-layout
      v-if="!loading"
      row
      wrap>
      <v-flex md12>
        <v-form>
          <v-card class="elevation-0">
            <v-card-title>
              <h3 class="headline mb-0">Identificação</h3>
            </v-card-title>
            <v-card-text>
              <v-layout
                row
                wrap>
                <v-flex md9>
                  <v-text-field
                    v-validate="'required'"
                    v-model="form.nome"
                    :error-messages="errors.collect('Nome Rede')"
                    data-vv-name="Nome Rede"
                    label="Nome Rede"
                    required
                  />
                </v-flex>
                <v-flex md3>
                  <v-text-field
                    v-validate="'required'"
                    v-model="form.sigla"
                    :error-messages="errors.collect('Sigla')"
                    label="Sigla"
                    type="text"
                    data-vv-name="Sigla"
                    required
                  />
                </v-flex>
              </v-layout>
            </v-card-text>
          </v-card>

          <br>

          <v-alert
            :value="true"
            type="warning"
          >
            <span class="subheading">As regras cadastradas na Rede serão aplicadas para todas as Empresas e Lojas dessa Rede.</span>
          </v-alert>

          <br>

          <template v-if="!!form.id && !acao">
            <v-card>
              <v-toolbar
                flat
                color="white">
                <v-toolbar-title>Regras da Rede</v-toolbar-title>
                <v-divider
                  class="mx-2"
                  inset
                  vertical />
                <v-spacer />
                <v-btn
                  small
                  fab
                  color="primary">
                  <v-icon @click="newRegra()">mdi-plus</v-icon>
                </v-btn>
              </v-toolbar>
              <v-progress-linear
                :active="loading"
                color="light-blue"
                indeterminate
                height="3"
              />
              <v-data-table
                :headers="headers"
                :items="regras"
                :total-items="totalItems"
                :pagination.sync="pagination"
                class="elevation-1 arrow-pagintion"
              >
                <template
                  slot="items"
                  slot-scope="{ item }"
                  ma-5
                >
                  <core-tr-item-regra
                    :item="item"
                    :visualizar-regra="visualizarRegra"
                    :alterar-regra="editarRegra"
                    :reload="reload"
                  />
                </template>
              </v-data-table>
            </v-card>
            <v-layout class="justify-end">
              <template>
                <v-btn
                  outline
                  style="color: black !important"
                  @click="cancelar">Cancelar</v-btn>
                <v-btn
                  :loading="loadingBtn"
                  color="primary"
                  @click="validateBeforeSubmit"
                >Salvar</v-btn>
              </template>
            </v-layout>
          </template>
          <template v-else>
            <v-card
              v-if="!disabled"
              class="elevation-0">
              <v-card-text>
                <v-autocomplete
                  v-model="copyRedeId"
                  :items="redes"
                  item-value="id"
                  item-text="descricao"
                  label="Copiar dados da regra vigente de outra rede"
                  @change="carregaDadosRedeCopy"
                />
              </v-card-text>
            </v-card>

            <core-regra-financeira
              v-if="!loading"
              v-model="regra"
              :disabled="disabled"
              :salvar="validateBeforeSubmit"
              :voltar="voltar"
              :cancelar="cancelar"
              :acao="acao"
              :primeira-regra="totalItems === 0"
            />
          </template>
        </v-form>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import { RedeBusiness, RegraFinanceiraBusiness, TiposBusiness } from '../../business'
import RegraFinanceira from '../../beans/RegraFinanceira'
import { ACAO_EDITAR, ACAO_NOVA_REGRA, ACAO_VISUALIZAR_REGRA } from '../../constants'

export default {
  metaInfo: {
    titleTemplate: '%s - Rede'
  },
  data () {
    return {
      copyRedeId: null,
      redes: [],
      form: {
        id: null,
        nome: null,
        sigla: null
      },
      regra: new RegraFinanceira(),
      headers: [
        { class: 'grey lighten-1', sortable: false, text: 'Vigente', align: 'center' },
        { class: 'grey lighten-1', sortable: false, text: 'Vigente Desde', value: 'dtVigencia' },
        { class: 'grey lighten-1', sortable: false, text: 'Multa atraso', align: 'right', value: 'peMultaPorAtraso' },
        { class: 'grey lighten-1', sortable: false, text: 'Juros Mora atraso', align: 'right', value: 'peJurosMoraPorAtraso' },
        { class: 'grey lighten-1', sortable: false, text: 'Tarifa Parcela Banco', align: 'right', value: 'vlTarifaParcelaBanco' },
        { class: 'grey lighten-1', sortable: false, text: 'Tarifa Parcela Loja', align: 'right', value: 'vlTarifaParcelaLoja' },
        { class: 'grey lighten-1', sortable: false, text: 'Cobrar Carnê em Loja', align: 'right', value: 'cobrarCarneLoja' },
        { class: 'grey lighten-1', sortable: false, text: 'Incluída Por', value: 'nomeUsuario' },
        { class: 'grey lighten-1', sortable: false, text: '', value: '', align: 'center' }
      ],
      loading: false,
      totalItems: 0,
      pagination: {
        rowsPerPage: 10,
        sortBy: 'dataVigencia'
      },
      filtros: {
        redeId: null
      },
      regras: [],
      valid: false,
      acao: null,
      disabled: false,
      loadingBtn: false,
      const_nova_regra: ACAO_NOVA_REGRA,
      const_view_regra: ACAO_VISUALIZAR_REGRA
    }
  },
  watch: {
    pagination: {
      handler () {
        this.paginar()
      }
    }
  },
  beforeMount () {
    this.reload()
  },
  methods: {
    validateBeforeSubmit () {
      this.$validator.validate()
        .then(result => {
          if (result) {
            this.loadingBtn = true
            var msgSucesso = !this.form.id ? 'Cadastro realizado com sucesso' : 'Atualização realizada com sucesso'
            RedeBusiness.salvar(this.form)
              .then(response => {
                this.form = response.data
                this.regra.redeId = this.form.id
                if (this.acao === this.const_nova_regra) { // só pode salvar a regra se ela for nova, ou seja, id dela é null
                  RegraFinanceiraBusiness.salvar(this.regra)
                    .then(response => {
                      this.$root.showSucesso(msgSucesso)
                      this.loadingBtn = false
                      this.reset()
                    })
                    .catch(error => {
                      this.$root.showErro(error.data)
                      this.loadingBtn = false
                    })
                } else {
                  this.$root.showSucesso(msgSucesso)
                  this.loadingBtn = false
                }
              })
              .catch(error => {
                this.$root.showErro(error.data)
                this.loadingBtn = false
              })
          } else {
            this.$root.showAlerta(this.$vuetify.t('$vuetify.Erro.camposObrigatorios'))
          }
        })
    },
    cancelar () {
      this.$router.push('/lista-rede')
    },
    voltar () {
      this.reset()
    },
    visualizarRegra (regra) {
      this.disabled = true
      RegraFinanceiraBusiness.getById(regra.id)
        .then(response => {
          this.regra = response.data
          this.regra.dataVigencia = this.$moment(response.data.dataVigencia).format('YYYY-MM-DD')
          this.regra.dataVigenciaLancamento = this.$moment(response.data.dataVigenciaLancamento).format('YYYY-MM-DD')
          this.acao = this.const_view_regra
        })
        .catch(() => this.$root.showAlerta('Problemas para recuperar os dados da regra, tentar mais tarde'))
    },
    editarRegra (regra) {
      RegraFinanceiraBusiness.getById(regra.id)
        .then(response => {
          this.regra = response.data
          this.regra.dataVigencia = this.$moment(response.data.dataVigencia).format('YYYY-MM-DD')
          if (response.data.dataVigenciaLancamento) {
            this.regra.dataVigenciaLancamento = this.$moment(response.data.dataVigenciaLancamento).format('YYYY-MM-DD')
          }
          this.acao = ACAO_EDITAR
        })
        .catch(() => this.$root.showAlerta('Problemas para recuperar os dados da regra, tentar mais tarde'))
    },
    carregaDadosRedeCopy (redeId) {
      this.loading = true
      RedeBusiness.getRegrafinanceiraVigenteById(redeId)
        .then(response => {
          this.regra = { ...response.data }
          this.regra.dataVigencia = this.$moment().format('YYYY-MM-DD')
          this.regra.dataVigenciaLancamento = this.$moment().format('YYYY-MM-DD')
          this.regra.id = null
          this.regra.isVigente = false
        })
        .catch(erro => {
          this.$root.showAlerta('Problemas para copiar dados: ' + erro)
        })
        .finally(() => {
          this.loading = false
        })
    },
    newRegra () {
      this.acao = this.const_nova_regra
      this.regra = new RegraFinanceira()
      TiposBusiness.getAllRedes().then(response => {
        this.redes = response.data
      })
    },
    paginar () {
      if (!this.filtros.redeId) {
        this.filtros.redeId = this.form.id
      }
      RegraFinanceiraBusiness.pagination(this.pagination.rowsPerPage, this.pagination.page - 1, this.pagination.sortBy || 'dataVigencia', this.filtros)
        .then(response => {
          this.regras = response.data.content
          this.totalItems = response.data.totalElements
        })
        .catch(() => this.$root.showErro('Erro ao carregar as regras da rede'))
    },
    reset () {
      this.acao = null
      this.disabled = false
    },
    reload () {
      this.reset()
      let id = this.$route.params.id
      if (id) {
        this.loading = true
        this.filtros.redeId = id
        RedeBusiness.getById(id)
          .then(response => {
            this.form = response.data
            this.paginar()
          }).finally(() => {
            this.loading = false
          })
      } else {
        this.newRegra()
      }
    }
  }
}
</script>
